import Vue from 'vue'
import Router from 'vue-router'
import ShoppingCart from '@/pages/ShoppingCart'
import aaa from '@/pages/aaa'
import ccc from '@/pages/ccc'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: {
        name:'ShoppingCart'
      }
    },
    {
      path:'/ShoppingCart',
      name:'ShoppingCart',
      component:ShoppingCart,
      meta:{

      }
    },
    {
      path:'/aaa',
      name:'aaa',
      component:aaa,
      meta:{

      }
    },
    {
      path:'/ccc',
      name:'ccc',
      component:ccc,
      meta:{

      }
    },
  ]
})



